<template>
  <Row class="p-lr-30 h-140 w-100b rup-header" x="e" y="c">
    <!-- <img class="w-158 h-42" src="@/image/user/bifinancelogo.webp" @click="toLink"> -->
    <Row y="c" @click="showPicker = true">
      <Row class="font-28 bold c-white">{{ columns.find((obj)=>obj.value===fieldValue)?.label }}</Row>
      <IconSvg name="icon-invite_select" class="font-20 c-white m-t-4 m-l-8" />
    </Row>

    <van-popup v-model:show="showPicker" round position="bottom">
      <Row
        v-for="(item,index) in columns"
        :key="index"
        :class="index+1!==columns.length?'border-b':''"
        class="w-100b h-100 lang-item  font-32"
        x="c"
        @click="onConfirm(item)"
      >
        {{ item.label }}
      </Row>
      <Row class="h-10 w-100b bg-d" />
      <Row class="w-100b h-100 lang-item font-32 c-info" x="c" @click="showPicker=false">
        {{ $t('alert.cancel') }}
      </Row>
    </van-popup>
  </Row>
</template>
<script setup>
const languageStore = useLanguageStore()

let fieldValue = $ref(languageStore.locale)
const showPicker = $ref(false)

const columns = $computed(() => {
  return languageStore.languageMap
})

const toLink = () => {
  const url = '/home'
  window.open(url, '_blank')
}

const onConfirm = (item) => {
  // showPicker = false
  fieldValue = item.value
  localStorage.locale = item.value
  replaceParamVal('locale', item.value)
  location.reload()
}

onBeforeMount(() => {
  document.title = $t('user.ruldTitle')
})

onBeforeUnmount(() => {
  document.title = 'bifinance'
})

</script>
<style lang="scss">
@import '@/sass/var/index.scss';
.rup-header{
  --van-popup-round-radius:8px;
  .lang-item{
    border-bottom: 1px solid $font-placeholder-color;
  }
}

</style>
